.choose-img {
  position: absolute;
  height: 300px;
  width: 50px;
  top: 100px;
  left: 20px;
  z-index: 100;
  overflow: hidden;
  /*transition: all .3s;*/
}

.imgurl {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(0,0,0,0.3);
  border-radius: 50%;
  display: block;
  cursor: pointer;
  z-index: 100;
  transition: all .3s;
  position: absolute;
  left: 12px;
  z-index: 50;
  background-clip: border-box;
  /*background-color: #0a51e2;*/
}

.choose-img span:nth-of-type(1) {
  /*background-color: white;*/
}

.choose-img span:nth-of-type(2) {
  top: 24px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(3) {
  top: 0px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(4) {
  top: 24px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(5) {
  top: 0px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(6) {
  top: 24px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(7) {
  top: 0px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(8) {
  top: 24px;
  opacity: 0;
  transition: all .5s;
}

.choose-img span:nth-of-type(9) {
  top: 0px;
  opacity: 0;
  transition: all .5s;
}

.choose-img:hover span:nth-of-type(1){
  opacity: 1;
}

.choose-img:hover span:nth-of-type(2){
  top: 24px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(3){
  top: 64px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(4){
  top: 88px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(5){
  top: 128px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(6){
  top: 152px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(7){
  top: 192px;
  opacity: 1;
}

.choose-img:hover span:nth-of-type(8){
  top: 216px;
  opacity: 1;
