.choose-img span:nth-of-type(1):hover {
  background: rgb(45,93,167);
  background-clip: padding-box;
  /*5.jpg*/
}

.choose-img span:nth-of-type(3):hover {
  background: rgb(236, 208, 193);
  background-clip: padding-box;
  /*3.jpg*/
}

.choose-img span:nth-of-type(5):hover {
