.music-wave {
  position: absolute;
  width: 75%;
  top: 120px;
  left:13%;
  right: 10%;
  opacity: 0;
  transition: opacity .3s;
  overflow: hidden;
  /*background: rgba(157, 179, 233, 0.8);*/
  /*box-shadow: 2px 0 2px rgba(0, 0, 0, 0.5), inset 0 0 5px rgba(255, 255, 255, 0.2);*/
}

.wave-center {
  width: 150px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left:  -75px;
  margin-top: -30px;
  font-size: 22px;
}

#canvas {
  /*width: 100%;
  height: 100%;*/
}



